<template>
    <div class="toast-warppar" v-if="isShow">
        <div class="toast-content">
            <img v-if="icon == 'success'" src="../images/mobile/img_finish.png" alt="" class="icon">
            <div class="toast-text">{{txt}}</div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
       text: {
           default: '',
           type: String
       } 
    },
    data(){
        return {
            defalutIcon: 'success',
            icon: 'success',
            txt: '',
            isShow: false,
            delayed: 1200
        }
    },
    methods:{
        show(opts={}){
            let {delayed,icon,content} = opts;
            this.isShow = true;
            this.icon = icon || this.defalutIcon;
            this.txt = content || this.text;
            if(delayed != -1){
                clearTimeout(this.showTime);
                this.showTime = setTimeout(() => {
                    this.hide();
                }, delayed || this.delayed );
            }
        },
        hide(){
            clearTimeout(this.showTime);
            this.isShow = false;
        }
    }
}
</script>
