<script>
import bannerBtnGroup from '@components/banner-btn-group'
import toast from '@components/toast'
import qrcodeBox from '@components/qrcode-box'
import toastQrcode from '@components/toast-qrcode'
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
    components:{
        bannerBtnGroup,
        toast,
        qrcodeBox,
        toastQrcode
    },
    data(){
        return{
            isShowMenu: false,
            menuLeaving: false,
            MenuListTime: null,
            hoveringBox: 0,
            activeIndex: 0,
        }
    },
    methods: {
        bannerBtnClick(type){
            switch(type){
                case 'email': 
                    this.copyEmail();
                    break;
                case 'contact': 
                    this.$refs['toast_service_qrcode'].show()
                    break;
                default: 
                    break;
            }
        },
        toWeibo(){
            window.open('https://weibo.com/234885158')
        },
        contactWx(){
            this.$refs['toast_subscription_qrcode'].show()
        },
        copyEmail(){
            this.copyText('info@yyyad.com');
            this.$refs['toast'].show({ content: '已复制邮箱' });
        },
        copyPhone(){
            this.copyText('02031066369');
            this.$refs['toast'].show({ content: '已复制电话号码' });
        },
        copyText(text){
            if(window.clipboardData){
                window.clipboardData.setData('text', text)
                return;
            }
            if(window.document.createRange){
                const range = window.document.createRange(); 
                let createSeleNode = document.createElement('div');
                createSeleNode.innerHTML = text;
                createSeleNode.id = 'will_copy_text';
                window.document.body.appendChild(createSeleNode);
                range.selectNode(createSeleNode);
                const selection = window.getSelection(); 
                if(selection.rangeCount > 0) selection.removeAllRanges();
                selection.addRange(range); 
                window.document.execCommand('copy');
                window.document.body.removeChild(window.document.getElementById('will_copy_text'))
                return;
            }
            var oInput = window.document.createElement('input');
            oInput.id = "clipboard_input"
            oInput.value = text;
            // oInput.setAttribute('readonly','')
            window.document.body.appendChild(oInput);
            oInput.select(); // 选择对象
            window.document.execCommand('Copy'); // 执行浏览器复制命令
            oInput.className = 'oInput';
            oInput.style.display = 'none';
            
            window.document.body.removeChild(window.document.getElementById('clipboard_input'))
        },
        nav(idName){
            if(isFinite(idName)){
                if(idName == -1){
                    window.scrollTo(0,window.document.body.scrollHeight);
                }else{
                    window.scrollTo(0,idName);
                }
            }else{
                let targetDoc = window.document.getElementById(idName)
                var scale = window.document.documentElement.clientWidth / 750;
                window.scrollTo(0,targetDoc.offsetTop - scale * 88);
            }
            this.showMenu(false);
        },
        showMenu(blo=true){
            if(blo === false){
                this.menuLeaving = true;
                this.MenuListTime = setTimeout(() => {
                    this.$nextTick(()=>{
                        this.menuLeaving = false;
                        this.isShowMenu = false;
                    })
                }, 300);                
            }else{
                clearTimeout(this.MenuListTime);
                this.isShowMenu = blo;
            }
        },
        showServiceContent(idx){
            this.hoveringBox = idx === this.hoveringBox ? 0 : idx
        }
    },
    mounted(){
        let that = this;
        this.swiper = new Swiper('.swiper-container', {
            direction: 'horizontal',
            resistanceRatio: 0,
            watchSlidesProgress: true,
            loop : true,
            // autoplay:true,
            on: {
                transitionStart: function () {
                    // console.log(this.activeIndex);
                    that.$nextTick(()=>{
                        that.activeIndex = this.activeIndex;
                    })
                }
            },
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
        });
    }
}
</script>
